<template>
  <div class="profile-main">
    <el-row>
      <el-col :xs="24" :sm="24" :md="15" :lg="14" :xl="14">
        <el-card class="box-card" v-loading="studentLoading">
          <div class="card-title"></div>
          <el-row>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.name}}</span>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.stuNo}}</span>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.wechat}}</span>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.age}}</span>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.phone}}</span>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.major}}</span>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.corpPosition}}</span>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.corpName}}</span>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title">qq</span>
                <span class="item-content">{{studentInfo.qq}}</span>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.college}}</span>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.gmtStart}}</span>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.gmtEnd}}</span>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <div class="text item">
                <span class="item-title"></span>
                <span class="item-content">{{studentInfo.idCard}}</span>
              </div>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
      <el-col class="hidden-sm-and-down" :xs="24" :sm="24" :md="9" :lg="9" :offset="1" :xl="9">
        <el-card class="box-card" v-if="hasTeacher" v-loading="teacherLoading">
          <div class="card-title"></div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.name}}</span>
          </div>

          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.teacherNo}}</span>
          </div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.sex}}</span>
          </div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.age}}</span>
          </div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.college}}</span>
          </div>
        </el-card>

        <el-card class="box-card" v-if="!hasTeacher" v-loading="teacherLoading">
          <div class="card-title" style="margin-bottom:18px;"></div>
          <el-select v-model="teacherNo" placeholder="">
            <el-option
              v-for="item in teachers"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
              <span style="float: left">{{ item.label }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
            </el-option>
          </el-select>
          <div style="margin-top:18px;">
            <el-button type="primary" @click="selectTeacher"></el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-row class="hidden-sm-and-up">
      <el-col class :xs="24" :sm="24" :md="9" :lg="9"  :xl="9">
        <el-card class="box-card" v-if="hasTeacher" v-loading="teacherLoading">
          <div class="card-title"></div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.name}}</span>
          </div>

          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.teacherNo}}</span>
          </div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.sex}}</span>
          </div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.age}}</span>
          </div>
          <div class="text item">
            <span class="item-title"></span>
            <span class="item-content">{{teacherInfo.college}}</span>
          </div>
        </el-card>

        <el-card class="box-card" v-if="!hasTeacher" v-loading="teacherLoading">
          <div class="card-title" style="margin-bottom:18px;"></div>
          <el-select v-model="teacherNo" placeholder="">
            <el-option
              v-for="item in teachers"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
              <span style="float: left">{{ item.label }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
            </el-option>
          </el-select>
          <div style="margin-top:18px;">
            <el-button type="primary" @click="selectTeacher"></el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import {
  getStudentInfo,
  getTeacherInfo,
  selectTeacher,
  selectTeacherByNo
} from "../../network";
import { replaceNull } from "../../command/utils";
export default {
  //import
  components: {},
  data() {
    //
    return {
      studentInfo: {},
      teacherInfo: {},
      teacherLoading: false,
      studentLoading: false,
      hasTeacher: true,
      teachers: [],
      teacherNo: ""
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    getInfo() {
      this.studentLoading = true;
      this.teacherLoading = true;
      getStudentInfo()
        .then(res => {
          console.log(res);
          this.studentInfo = Object.assign({}, res.data.data, {});
          this.studentInfo = replaceNull(this.studentInfo);
          // console.log(this.studentInfo)
        })
        .catch(() => {
          this.$message.error("!");
        })
        .finally(() => {
          this.studentLoading = false;
        })
        .then(() => {
          console.log(this.studentInfo.teacherNo);
          if (this.studentInfo.teacherNo !== "") {
            getTeacherInfo().then(res => {
              console.log(res);
              this.hasTeacher = true;
              this.teacherInfo = Object.assign({}, res.data.data, {});
              this.teacherInfo = replaceNull(this.teacherInfo);
              // console.log(this.teacherInfo)
            });
          } else {
            // console.log(111)
            selectTeacher().then(res => {
              // console.log(res)
              this.hasTeacher = false;
              if (res.data.status == 1) {
                this.teachers = [];
                res.data.data.map(item => {
                  this.teachers.push({
                    value: item.teacherNo,
                    label: item.name
                  });
                });
              }
            });
          }
        })
        .catch(() => {
          this.$message.error("!");
        })
        .finally(() => {
          this.teacherLoading = false;
        });
    },
    selectTeacher() {
      // console.log(11)
      if (!this.teacherNo) {
        this.$message.error("!");
        return;
      }
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          selectTeacherByNo({ tNo: this.teacherNo }).then(res => {
            // console.log(res)
            if (res.data.status == 1) {
              this.$message.success("!");
              this.getInfo();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.getInfo();
    selectTeacher().then(res => {
      // console.log(res);
    });
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
  deactivated() {} //keep-alive,
};
</script>
<style scoped>
.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.card-title {
  font-weight: bold;
  color: #333;
  padding: 10px 0;
  border-bottom: 1px solid #dddddd;
}
.item-title,
.item-content {
  display: inline-block;
}
.item-content {
  width: 150px;
  word-break: break-word;
}
.item-title {
  width: 60px;
  /* text-align: right; */
  vertical-align: top;
  /* font-weight: bold; */
  color: rgb(97, 113, 139);
  margin-right: 10px;
}
.profile-main > .el-row {
  display: flex;
  justify-content: center;
}
</style>